document.addEventListener( "DOMContentLoaded", function() {
	const layouts = this.querySelector( '.sidebox.layouts' );

	if ( layouts ) {
		AdminColumns.Layouts = new Layouts( layouts );
	}

} );

class Layouts {

	constructor( el ) {
		this.el = el;
		this.list_screen = el.dataset.type;
		this.button_add = this.el.querySelector( 'a.add-new' );
		this.createForm = new NewLayoutForm( this.el.querySelector( '.new' ) );
		this._layouts = new Map();

		this.getLayouts();
		this.initEvents();
	}

	getLayouts() {
		this.el.querySelectorAll( '.item.layout' ).forEach( layout => {
			const key = layout.dataset.screen;
			this._layouts.set( key, new Layout( layout, key, this.list_screen ) );
		} );
	}

	initEvents() {
		this.button_add.addEventListener( 'click', e => {
			e.preventDefault();

			if ( this.button_add.classList.contains( 'open' ) ) {
				this.cancelNewLayout();
			} else {
				this.addNewLayout();
			}

		} );

		jQuery( this.el ).sortable( {
			items : '.item.layout',
			axis : 'y',
			containment : jQuery( this.el ),
			handle : '.cpacicon-move',
			stop : () => {
				this.setNewOrder();
			}
		} );

		// Store layouts on main update event
		jQuery( document ).on( 'AC_Form_AfterUpdate', () => {
			this._layouts.forEach( ( layout ) => {
				layout.saveSettings();
			} );
		} );

	}

	setNewOrder() {
		let order = [];
		this.el.querySelectorAll( '.item.layout' ).forEach( layout => {
			order.push( layout.dataset.screen );
		} );

		this.storeLayoutOrder( order );
	}

	storeLayoutOrder( order ) {
		return jQuery.ajax( {
			url : ajaxurl,
			method : 'POST',
			data : {
				_ajax_nonce : acp_layouts._nonce,
				action : 'acp_update_layout_order',
				list_screen : this.list_screen,
				order : order
			}
		} );
	}

	addNewLayout() {
		this.button_add.classList.add( 'open' );
		this.createForm.open();
	}

	cancelNewLayout() {
		this.button_add.classList.remove( 'open' );
		this.createForm.close();
	}

}

class Layout {

	constructor( el, key, list_screen ) {
		this.el = el;
		this.key = key;
		this.list_screen = list_screen;

		this.initEvents();
	}

	isReadOnly(){
		return this.el.classList.contains('read_only');
	}

	initEvents() {
		this.el.querySelector( '.cpacicon-move' ).addEventListener( 'click', e => e.stopPropagation() );

		// Toggle body
		this.el.querySelector( '.head ' ).addEventListener( 'click', e => {
			e.preventDefault();

			this.isOpen() ? this.close() : this.open();
		} );

		this.el.querySelectorAll( '.head input, .head a' ).forEach( el => {
			el.addEventListener( 'click', e => {
				e.stopPropagation();
			} );
		} );


		let saveButton = this.el.querySelector('.save');
		if( saveButton ){
			saveButton.addEventListener('click', () => {
				this.saveSettings();
			} );
		}

		[ 'change', 'keyup' ].forEach( event => {
			let input = this.el.querySelector( 'input.name' );
			input.addEventListener( event, ( e ) => {
				let value = e.target.value;

				if ( value === '' ) {
					value = input.dataset.value;
				}

				this.el.querySelector( '.head .title' ).innerText = value;
			} );
		} );

		userSelect( jQuery( this.el ).find( 'select.users' ) );
		roleSelect( jQuery( this.el ).find( 'select.roles' ) );
	}

	isOpen() {
		return this.el.classList.contains( 'open' );
	}

	open() {
		this.el.classList.add( 'open' );
		jQuery( this.el.querySelector( '.body' ) ).slideDown( 200 );
	}

	close() {
		this.el.classList.remove( 'open' );
		jQuery( this.el.querySelector( '.body' ) ).slideUp( 200 );
	}

	getData() {
		return jQuery( this.el.querySelector( '.body form' ) ).serialize();
	}

	startLoading() {
		this.el.classList.add( 'loading' );
	}

	stopLoading() {
		this.el.classList.remove( 'loading' );
	}

	getNameElement() {
		return this.el.querySelector( 'input.name' );
	}

	saveSettings() {
		let self = this;
		let name_element = this.getNameElement();
		let $layout = jQuery( this.el );

		if( this.isReadOnly() ){
			return;
		}

		self.startLoading();

		jQuery.post( ajaxurl, {
				action : 'acp_update_layout',
				_ajax_nonce : acp_layouts._nonce,
				list_screen : this.list_screen,
				data : self.getData(),
			},
			// JSON or empty response
			( response ) => {

				if ( response ) {
					if ( response.success ) {
						name_element.dataset.value = name_element.value; // when input is empty we use the data attribute as a fallback

						$layout.find( '.save-message' ).slideDown().delay( 1000 ).slideUp();
						$layout.find( '.title-div span.description' ).text( response.data.title_description );
					} else if ( response.data ) {
						if ( 'empty-name' === response.data ) {
							$layout.addClass( 'open' ).find( '.body' ).slideDown().find( '.row.name' ).addClass( 'save-error' );
						}
					}
				}

			}, 'json'
		).always( () => {
			self.stopLoading();
		} );
	}
}

class NewLayoutForm {

	constructor( el ) {
		this.el = el;
		this.initEvents();
	}

	open() {
		jQuery( this.el ).slideDown();
	}

	close() {
		jQuery( this.el ).slideUp();
	}

	initEvents() {
		// Add new error message
		this.el.querySelector( '.new form' ).addEventListener( 'submit', ( e ) => {
			const name = this.el.querySelector( '.row.name input' ).value;

			if ( !name ) {
				e.preventDefault();
				this.el.querySelector( '.row.name' ).classList.add( 'save-error' );
			}

		} );

		userSelect( jQuery( this.el ).find( 'select.users' ) );
		roleSelect( jQuery( this.el ).find( 'select.roles' ) );
	}
}

const userSelect = ( $el ) => {
	$el.ac_select2( {
		placeholder : acp_layouts.users,
		multiple : true,
		theme : 'acs2',
		minimumInputLength : 0,
		escapeMarkup : function( text ) { return jQuery( '<div>' + text + '</div>' ).text(); },
		ajax : {
			type : 'POST',
			url : ajaxurl,
			dataType : 'json',
			delay : 350,
			data : function( params ) {

				return {
					action : 'acp_layout_get_users',
					_ajax_nonce : acp_layouts._nonce,
					search : params.term,
					page : params.page
				};
			},
			processResults : function( response ) {
				if ( response ) {
					if ( response.success && response.data ) {
						return response.data;
					}
				}

				return { results : [] };
			},
			cache : true
		}
	} );
};

const roleSelect = ( $el ) => {

	$el.each( function() {
		jQuery( this ).ac_select2( {
			placeholder : acp_layouts.roles,
			theme : 'acs2'
		} )
			.on( "select2:select", function() {
				jQuery( this ).ac_select2( 'open' );
			} )
			.on( "select2:open", function() {
				setTimeout( function() {
					jQuery( '.select2-container.select2-container--open .select2-dropdown li[role=group]' ).each( function() {
						if ( jQuery( this ).find( 'li[aria-selected=false]' ).length > 0 ) {
							jQuery( this ).show();
						} else {
							jQuery( this ).hide();
						}
					} );
				}, 1 );
			} );
	} );
};




